import { PropsWithChildren } from "react"
import { ListSorterDirection, OnListSorterDirectionChange } from "@rp/common/utils"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { HubSortableHeaderDesktopCell } from "./HubSortableHeaderDesktopCell"
import { HubSortableHeaderMobileCell } from "./HubSortableHeaderMobileCell"
import { SortOption } from "./hubTable.types"

function HubSortableHeadCell<T>({
  value,
  sortBy,
  direction,
  onChange,
  label = "",
  sortOptions,
  defaultDirection = "asc",
  shouldPreventMobileFormat = false,
}: PropsWithChildren & {
  defaultDirection?: ListSorterDirection
  value: keyof T
  label?: string
  direction: ListSorterDirection
  sortBy: keyof T
  sortOptions: Array<SortOption>
  onChange: (props: OnListSorterDirectionChange<T>) => void
  shouldPreventMobileFormat?: boolean
}) {
  const { isMobile: isMobileFromDevice } = useDeviceInfo()
  const isMobile = !shouldPreventMobileFormat && isMobileFromDevice

  return (
    <Box sx={{ display: "flex", alignItems: "center" }}>
      {isMobile ? (
        <HubSortableHeaderMobileCell
          direction={direction}
          sortBy={sortBy}
          sortOptions={sortOptions}
          onDirectionChange={direction => {
            onChange({ sortBy, direction })
          }}
          onSortChange={sortBy => {
            onChange({ sortBy, direction: defaultDirection })
          }}
        />
      ) : (
        <HubSortableHeaderDesktopCell
          onDirectionChange={direction => {
            onChange({ sortBy: value, direction })
          }}
          sortVisible={sortBy === value}
          direction={direction}
          defaultDirection={defaultDirection}
        >
          {label}
        </HubSortableHeaderDesktopCell>
      )}
    </Box>
  )
}

export { HubSortableHeadCell }
